* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Varela Round', sans-serif;
  font-size: 15px;
  direction: rtl;
}

section {
	clear: both;
	padding: 0px;
	margin: 0px;
}

a {
  display: block;
  text-decoration: none;
  color: #2d2d2d;
  outline: 0;
  box-shadow: unset;
  transition: 0.5s;
}

a:hover, a:focus {
  text-decoration: none;
  color: #8FD3E8;
}

.is-active {
  color: #8FD3E8;
}

h1 {
  color: #8FD3E8;
  margin-bottom: 15px;
  font-size: 33px;
}

 .h2, .h3, .h4, .h5, .h6, h2, h3, h4, h5, h6 {
  color: #000000;
  margin-bottom: 15px;
}

.h1:last-child, .h2:last-child, .h3:last-child, .h4:last-child, .h5:last-child, .h6:last-child, h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child {
  margin-bottom: 0;
}

p {
  font-size: 15px;
  color: #646464;
  line-height: 1.8;
  margin-bottom: 15px;
}

p:last-child {
  margin-bottom: 0;
}

button {
  font-weight: 500;
  border: none;
  padding: 8px 30px;
  font-size: 13px;
  color: white;
  background: #8FD3E8;
  cursor: pointer;
  transition: 0.5s;
  -webkit-transition: 0.5s;
}

button:hover {
  background: rgb(98, 155, 173);
}

@media only screen and (max-width: 767px){p{padding:0 15px}h1{font-size:30px;padding:0 15px}.section-slide-show .banner-wrapper{height:200px !important}.section-slide-show .banner-wrapper img{height:130px !important;left:10% !important}.section-slide-show .banner-wrapper h1{padding-left:90px}.section-slide-show .banner-wrapper .center{top:40% !important}.section-slide-show .banner-wrapper .center h1{font-size:20px !important;line-height:30px !important}.about-us-page-container .about-the-team-section{padding:50px 35px}}@media screen and (min-device-width: 1200px) and (max-device-width: 1600px) and (-webkit-min-device-pixel-ratio: 1){.section-features .features-header{padding:0 150px}}

.section-slide-show{width:100%}.section-slide-show .banner-wrapper{position:relative;height:530px;width:100%;margin:0 auto}.section-slide-show .banner-wrapper img{position:absolute;height:400px;width:auto;margin:0;top:50%;left:10%;transform:translateY(-50%)}.section-slide-show .banner-wrapper .center{margin:0;position:absolute;top:50%;left:60%;transform:translate(-50%, -50%)}.section-slide-show .banner-wrapper .center h1{font-size:50px;line-height:80px;color:white}.section-slide-show .banner-wrapper1{position:relative;height:530px;width:100%;background:red}

.feature-card{margin:0px 0px auto;display:flex;flex-direction:column;align-items:center;text-align:center}.feature-card .card-icon{margin-bottom:20px}.feature-card .card-icon img{height:67px;width:auto}.feature-card .card-content{margin-bottom:15px;width:75%}.feature-card .card-content h2{color:#8FD3E8;margin-bottom:10px;margin-top:0}.feature-card .card-content p{margin:0;font-size:13px}

.section-features{width:100%}.section-features .features-header{margin-bottom:70px}.section-features .features-header h1{margin-top:70px}.section-features .features-header p{font-size:17px}.section-features .features-content{width:75%;margin:0 auto;display:grid;grid-template-columns:repeat(auto-fit, minmax(250px, 1fr));grid-template-rows:auto;grid-row-gap:50px;row-gap:50px;margin-bottom:50px}


.image-banner-wrapper{width:100%;height:500px;background-size:cover;background-repeat:no-repeat;background-position:50% 50%;background-attachment:fixed;min-height:100vh}@media only screen and (max-width: 767px){.image-banner-wrapper{min-height:70vh;background-attachment:unset}}

.card{width:200px;margin:10px;letter-spacing:.5px}.card .img{height:70%}.card .img img{width:100%;height:100%}.card .card-description{height:30%;background:#8FD3E8}.card .card-description h3,.card .card-description p{padding:0;margin:0;color:white}.card .card-description h3{padding-top:15px}

.about-the-team-section,.team-managers-section{padding:50px 70px}.team-managers-section{background-color:#f7f7f7}.team-managers-section h1{margin-bottom:40px}.team-managers-section .manager-cards{width:80%;margin:0 auto;margin-bottom:30px;display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}


.toggle-button{display:flex;flex-direction:column;justify-content:space-around;height:24px;width:30px;background-color:transparent;border:none;cursor:pointer;padding:0}.toggle-button:focus{outline:none}.toggle-button .toggle-button-line{width:30px;height:2px;background-color:#8FD3E8}

.header-container{width:100%;position:relative;padding-top:15px;box-shadow:inset 0 8px 6px -6px rgba(0,0,0,0.2)}.header-container .side-menu-button{display:none;position:absolute;width:8%;margin-right:30px;top:25px}.header-container .login-user-link{position:absolute;top:25px;left:40px;display:flex;align-items:center;cursor:pointer}.header-container .login-user-link span{font-size:13px;display:inline;color:#8FD3E8;margin-right:10px}.header-container .login-user-link img{height:23px;width:auto}.header-container .top-wrapper{width:100%;display:flex;flex-direction:column;align-items:center;margin-bottom:20px}.header-container .top-wrapper img{width:50px;height:50px}.header-container .top-wrapper h1{color:#8FD3E8}.header-container .botom-wrapper{width:100%;background:#f7f7f7;display:block}.header-container .botom-wrapper nav{width:55%;margin:0 auto}.header-container .botom-wrapper nav .nav-content{text-align:center}.header-container .botom-wrapper nav .nav-content ul{list-style:none;margin:0;padding:0;display:flex;flex-direction:row;align-items:center;justify-content:space-evenly;font-size:13px}.header-container .botom-wrapper nav .nav-content ul li{position:relative;display:inline-block;width:25%;padding:10px 0}.header-container .botom-wrapper nav .nav-content ul li .dropdown-content{display:none;top:36px;position:absolute;background-color:rgba(247,247,247,0.5);min-width:100%;box-shadow:0px 8px 16px 0px rgba(0,0,0,0.2);z-index:1}.header-container .botom-wrapper nav .nav-content ul li .dropdown-content .link{padding:8px 0}.header-container .botom-wrapper nav .nav-content ul li:hover .dropdown-content{display:block}.header-container .botom-wrapper nav .nav-content ul li p{margin:0}@media (max-width: 769px){.header-container{box-shadow:none}.header-container .side-menu-button{display:block}.header-container .login-user-link{display:none}.header-container .botom-wrapper{display:none}}.mobile{display:none}

.side-drawer{height:100%;background-color:white;box-shadow:1px 0 7px rgba(0,0,0,0.5);position:fixed;top:0;left:0;width:70%;max-width:400px;z-index:200;transform:translateX(-100%);transition:transform 0.3s ease-out;display:flex;flex-direction:column}.side-drawer .login-user-link{display:flex;align-items:center;cursor:pointer;height:25%;justify-content:center}.side-drawer .login-user-link span{font-size:16px;display:inline;color:#8FD3E8;margin-right:10px}.side-drawer .login-user-link img{height:28px;width:auto}.side-drawer ul{margin:0;padding:0;height:100%;list-style:none;display:flex;flex-direction:column}.side-drawer ul li{margin:0.5rem 0}.side-drawer .side-drawer-option{font-size:25px;cursor:pointer}.visible{transform:translateX(0)}@media (min-width: 769px){.side-drawer{display:none}}

.footer-container{width:100%;background:#8FD3E8;height:140px;display:flex;align-items:center}.footer-container .copyrigth-area{display:flex;flex-direction:column;justify-content:flex-start;text-align:start;padding-right:25%}.footer-container .copyrigth-area p{color:white;font-size:12px;margin-bottom:5px}.footer-container .copyrigth-area p span{font-size:15px}.footer-container .copyrigth-area .social-media-area a img{height:17px;width:auto}

.preloader{position:fixed;left:0;top:0;width:100%;height:100%;z-index:999999}.preloader .spinner{width:70px;height:70px;position:absolute;left:0;right:0;top:50%;transform:translateY(-50%);margin:-30px auto 0}.preloader .double-bounce1,.preloader .double-bounce2{width:100%;height:100%;border-radius:50%;background-color:#4b9cdb;opacity:0.6;position:absolute;top:0;left:0;-webkit-animation:sk-bounce 2.0s infinite ease-in-out;animation:sk-bounce 2.0s infinite ease-in-out}.preloader .double-bounce2{-webkit-animation-delay:-1.0s;animation-delay:-1.0s}@-webkit-keyframes sk-bounce{0%,100%{transform:scale(0)}50%{transform:scale(1)}}@keyframes sk-bounce{0%,100%{transform:scale(0)}50%{transform:scale(1)}}

.backdrop{position:fixed;height:100%;width:100%;top:0;left:0;background-color:rgba(0,0,0,0.5);z-index:100}


.App {
  text-align: center;
}


.go-top{position:fixed;cursor:pointer;bottom:35px;left:35px;font-size:25px;color:#ffffff;background-color:#2d2d2d;z-index:4;width:45px;text-align:center;height:45px;line-height:50px;border-radius:50%;transition:0.5s}.go-top img{height:22px;width:auto}

